        class="question"
      >
      {{item.text}}

      </div>

    </div>
  </div>
</template>

<script>
export default {
  props: {
    dataActualState: {}
  },
  methods: {
    actionAnswer(action) {
      this.$emit("actionanswer", action)
    }
  }
}
</script>

