<template>
  <div class="close-disclaimer">
    <button
    @click = buttonAction
    >{{name}}</button>
  </div>
</template>

<script>
export default {
  props: {
    name: String,
    action: {}
  },
  methods: {
    buttonAction() {
      this.$emit("action", this.action)
      // console.log(this.action);
    }
  }
}
</script>

